:root {
    --font-family-primary: 'Arial', sans-serif;
    --font-family-secondary: 'Roboto', sans-serif;
    --color-primary: #4CAF50;
    --color-primary-dark: #45a049;
    --color-secondary: #2196F3;
    --color-secondary-dark: #1976D2;
    --color-danger: #f44336;
    --color-danger-dark: #da190b;
    --color-text-primary: #333;
    --color-text-secondary: #555;
    --color-background-primary: #ffffff;
    --color-background-secondary: #f9f9f9;
    --color-border: #e0e0e0;
    --border-radius-small: 4px;
    --border-radius-medium: 6px;
    --border-radius-large: 8px;
    --border-radius-xlarge: 12px;
    --box-shadow-light: 0 2px 5px rgba(0, 0, 0, 0.05);
    --box-shadow-medium: 0 4px 6px rgba(0, 0, 0, 0.1);
    --box-shadow-heavy: 0 8px 16px rgba(0, 0, 0, 0.1);
    --transition-speed: 0.3s;
}

.audit-logs {
    margin-top: 20px;
    font-family: var(--font-family-primary);
}

.audit-logs table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;
    box-shadow: var(--box-shadow-medium);
    border-radius: var(--border-radius-large);
    overflow: hidden;
}

.audit-logs th,
.audit-logs td {
    border: 1px solid var(--color-border);
    padding: 12px;
    text-align: left;
    transition: background-color var(--transition-speed) ease;
}

.audit-logs th {
    background-color: var(--color-background-secondary);
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    color: var(--color-text-primary);
}

.audit-logs tr:nth-child(even) {
    background-color: var(--color-background-secondary);
}

.audit-logs tr:hover {
    background-color: #e9ecef;
}

.chat-submissions {
    margin-top: 20px;
    font-family: var(--font-family-secondary);
}

.chat-submissions table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;
    box-shadow: var(--box-shadow-medium);
    border-radius: var(--border-radius-large);
    overflow: hidden;
}

.chat-submissions th,
.chat-submissions td {
    border: 1px solid var(--color-border);
    padding: 12px;
    text-align: left;
    transition: background-color var(--transition-speed) ease;
}

.chat-submissions th {
    background-color: var(--color-background-secondary);
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    color: var(--color-text-primary);
}

.chat-submissions tr:nth-child(even) {
    background-color: var(--color-background-secondary);
}

.chat-submissions tr:hover {
    background-color: #e9ecef;
}

.chat-submissions button {
    padding: 8px 16px;
    background-color: var(--color-primary);
    color: white;
    border: none;
    border-radius: var(--border-radius-small);
    cursor: pointer;
    transition: background-color var(--transition-speed) ease, transform 0.2s ease;
}

.chat-submissions button:hover {
    background-color: var(--color-primary-dark);
    transform: translateY(-2px);
}

.client-info-form {
    max-width: 800px;
    margin: 0 auto;
    padding: 30px;
    background-color: var(--color-background-primary);
    border: 1px solid var(--color-border);
    border-radius: var(--border-radius-xlarge);
    box-shadow: var(--box-shadow-heavy);
}

.client-info-form h2 {
    font-size: 2em;
    margin-bottom: 30px;
    color: var(--color-text-primary);
    border-bottom: 2px solid var(--color-primary);
    padding-bottom: 15px;
    text-align: center;
}

.client-info-form label {
    font-weight: bold;
    display: block;
    margin-bottom: 8px;
    color: var(--color-text-secondary);
}

.client-info-form input,
.client-info-form select {
    width: 100%;
    padding: 12px;
    margin-bottom: 20px;
    border: 1px solid var(--color-border);
    border-radius: var(--border-radius-medium);
    font-size: 1em;
    background-color: var(--color-background-secondary);
    transition: border-color var(--transition-speed) ease, box-shadow var(--transition-speed) ease;
}

.client-info-form input:focus,
.client-info-form select:focus {
    border-color: var(--color-primary);
    box-shadow: 0 0 0 2px rgba(76, 175, 80, 0.2);
    outline: none;
}

.client-info-form input[type="checkbox"] {
    width: auto;
    margin-right: 10px;
}

.client-info-form .checkbox-label {
    display: inline-block;
    margin-bottom: 15px;
}

.client-info-form button {
    padding: 12px 24px;
    margin-right: 15px;
    background-color: var(--color-primary);
    color: white;
    border: none;
    border-radius: var(--border-radius-medium);
    cursor: pointer;
    font-size: 1em;
    transition: background-color var(--transition-speed) ease, transform 0.2s ease;
}

.client-info-form button:hover {
    background-color: var(--color-primary-dark);
    transform: translateY(-2px);
}

.client-info-form button[type="button"] {
    background-color: var(--color-secondary);
}

.client-info-form button[type="button"]:hover {
    background-color: var(--color-secondary-dark);
}

.error {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(135deg, #ff6b6b, #ff8e8e);
    color: white;
    font-size: 1.5rem;
    font-weight: bold;
    text-align: center;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
    z-index: 9999;
    backdrop-filter: blur(10px);
    box-shadow: 0 0 50px rgba(255, 107, 107, 0.5);
}

.error::before {
    content: '⚠️';
    font-size: 3rem;
    margin-right: 15px;
}

.error::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: repeating-linear-gradient(45deg,
            rgba(255, 255, 255, 0.1),
            rgba(255, 255, 255, 0.1) 10px,
            rgba(255, 255, 255, 0.2) 10px,
            rgba(255, 255, 255, 0.2) 20px);
    pointer-events: none;
}

input:invalid {
    border-color: var(--color-danger);
}

.client-info-form .section {
    margin-bottom: 40px;
    padding: 20px 0;
    border-bottom: 1px solid var(--color-border);
}

.client-info-form .section:last-child {
