    border-collapse: collapse;
}

.audit-logs th,
.audit-logs td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
}

.audit-logs th {
    background-color: #f2f2f2;
    font-weight: bold;
}

.audit-logs tr:nth-child(even) {
    background-color: #f9f9f9;
}

.audit-logs tr:hover {
    background-color: #f5f5f5;
}

/* Chat Submissions Styles */
.chat-submissions {
    margin-top: 20px;
}

.chat-submissions table {
    width: 100%;
    border-collapse: collapse;
}

.chat-submissions th,
.chat-submissions td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
}

.chat-submissions th {
    background-color: #f2f2f2;
    font-weight: bold;
